.ico__circle {
  position: relative;
  border-radius: 50%;
  display: block;
}
.ico__circle::before {
  content: "";
  position: absolute;
  bottom: 0;
  top: 0;
  margin: auto;
  transform: rotate(45deg);
  transition: transform 0.3s;
}

